import axios from 'axios';

// 商品的数据请求
export default {
    namespaced:true,
    state:{
        //存放商品
        goodsAll:[],
        goodsPart:[],
        goodsCate:[],
        goods:[]
    },
    mutations:{
        setGoodsInfo(state,payload){
            state.goodsAll = payload.goodsAll;
            state.goodsPart = payload.goodsPart;
            
        },
        setGoodsCate(state,payload){
            state.goodsCate = payload;
        },
        setGoodsAll(state,payload){
            state.goods = payload;
        }

    },
    actions:{
        requestGoodsInfo(context,payload){
            axios.post('/pc/api/goods/goods_info',{
                index:payload,
            })
            .then(data =>{
                
                const {goodsAll,goodsPart} = data.data.data;
                // console.log(goodsAll,goodsPart);
                // 请求成功的时候
                context.commit('setGoodsInfo',{goodsAll,goodsPart});
            })

        },
        requestGoodsAll(context,payload){
            axios.post('/pc/api/goods/goods_info')
            .then(data =>{
                
                const {goodsAll} = data.data.data;
                // console.log(goodsAll);
                // 请求成功的时候
                context.commit('setGoodsAll',goodsAll);
            })

        },
        requestDelete(context,payload){
            axios.post('/pc/api/goods/goods_del',{
                id:payload
            })
            .then(data=>{
                
            })
        },
        requestAdd(context,payload){
            axios.post('/pc/api/goods/goods_add',{
                id:payload.goodsId,
                name:payload.goodsName,
                price:payload.goodsPrice,
                category:payload.category

            })
            .then(data=>{

            })
        },
        
       

        requireCategoryInfo(context,payload){
            axios.post('/pc/api/goods/goods_category',{
                category:payload,
            })
            .then(data=>{
                
                const {goodsCate} = data.data.data;
                console.log(goodsCate);
                context.commit('setGoodsCate',goodsCate);
            })
            .catch(err=>{
                console.log(err);
            })
        }
    }
}